@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: 'Mulish', sans-serif;
  box-sizing: border-box;
}

.navbar {
  background-color: white !important;
}

.navbar-brand{
  font-size: 1.9rem !important;
  color: #565389 !important;
}

.navbar a {
  font-size: 1.3rem;
  text-transform: capitalize;
}

.menu_active {
  font-weight: bold;
  border-bottom: 1px solid #565387;
}

.navbar a:hover {
  color: orangered !important;
}

@media (max-width : 991px) {
  .navbar-brand {
    font-size: 1.5rem;
  }

  .menu_active {
    font-weight: bold;
    border-bottom: none;
}
}

#header {
  width: 100%;
  height: 80vh;
  margin-top: 70px;
}

#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}

#header .brand-name{
  color: orangered;
}

#header h2{
  color: #484848;
  font-size: 24px;
}

#header .btn-buy-now{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display:inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: orangered;
  border: 2px solid orangered;
  text-decoration: none;
}

#header .btn-buy-now:hover {
  background-color: orangered;
  color: whitesmoke;
  font-weight: bolder;
}

.header-img{
  text-align: right;
}

#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

.image-fluid {
  width: 550px;
}

@-webkit-keyframes up-down {
  0% {
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
}

@keyframes up-down {
  0% {
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100% {
    -webkit-transform: translateY(-20px);
            transform: translateY(-20px);
  }
}

.card-img-top{
  height: 300px;
}

.contact_div{
  width:100%;
  height: 75vh;
}

footer p{
  padding: 0;
  margin: 0;
}

@media (max-width: 991px) {
  #header {
    height: 80vh;
    text-align: center;
  }

  #header .header-img {
    text-align: center;
  }

  #header .header-img img {
    width: 60%;
  }

  .card-img-top{
    height: 150px;
  }
 }

 @media (max-width: 768px) {
  #header {
    margin-top: 20px;
  }

  #header h1 {
    font-size: 28px;
  }

  #header h2 {
    font-size: 18px;
    margin-bottom: 30px;
  }

  #header .header-img img {
    width: 70%;
  }
 }
